body {
  background: linear-gradient(#4e54c8, #8f94fb)
}

#data-table {
  padding: 50px; 
}

.card{
  margin-bottom: 5rem;
}

.zabble-hed {
  text-align:center; 
  margin-top: 5rem;
  margin-bottom: 2rem;
  color: white;
}

thead {
  font-weight: bold;
}

.search-row{
  display: flex;
  flex-direction: row; 
  justify-content: flex-end;
  align-items: flex-end;
}

.input-col {
  margin-bottom: 1rem;
  text-align: right;
  font-weight: bold;
}

.footer-row{
  display: flex;
  justify-content: flex-end;
  text-align: right;
  margin-bottom: 2rem;
  margin-right: 1rem;
  color: #fff;
  font-weight: bold; 
}

a{
  color: #fff;
  border-bottom: 2px dashed #fff;
}

a:hover{
  text-decoration: none;
  color: #B8B8B8;
  cursor: pointer;
}